import Vue from 'vue'
import Router from 'vue-router'
import Entry from '@/components/Entry'

Vue.use(Router)

export default new Router({
    routes: [
        {
            // query params include [appId, appVersion, appConfigId, roomId, userId, userRole, mode]
            // mode 值可以为 livecast、playback、dev，表示直播模式、回放模式、开发模式
            path: '/',
            name: 'entry',
            component: Entry
        }
    ]
})



