<template>
    <div class="entry">
        <p>URL查询参数如下：</p>
        <ul>
            <li>appId: {{appId}}</li>
            <li>appVersion: {{appVersion}}</li>
            <li>appConfigId: {{appConfigId}}</li>
            <li>roomId: {{roomId}}</li>
            <li>userId: {{userId}}</li>
            <li>userRole: {{userRole}}</li>
            <li>mode: {{mode}}</li>
        </ul>
        <p>事件信息：</p>
        <ul>
            <li v-for="event in events">[{{event.name}}] {{event.desc}}</li>
        </ul>
    </div>
</template>

<script>
    import BoxBridge from '@tutor/box-bridge'
    import { WEB_APP_EVENT_READY, WEB_APP_EVENT_END } from '../constants/web-app-event'
    import { CLIENT_EVENT_ACTIVE, CLIENT_APP_EVENT_DESTROY } from '../constants/client-event'

    export default {
        name: 'Entry',
        data () {
            return {
                events: [],
                appId: null,
                appVersion: null,
                appConfigId: null,
                roomId: null,
                userId: null,
                userRole: null,
                mode: null
            }
        },
        methods: {
            addEventInfo (name, desc) {
                this.events.push({
                    name,
                    desc
                })
            }
        },
        mounted () {
            let query = this.$route.query
            this.appId = +query.appId
            this.appVersion = +query.appVersion
            this.appConfigId = query.appConfigId
            this.roomId = +query.roomId
            this.userId = +query.userId
            this.userRole = query.userRole
            this.mode = query.mode

            if (this.mode === 'dev') {
                BoxBridge.Util.mockByCsi()
            }

            const ClientApi = BoxBridge.ClientApi
            ClientApi.on(CLIENT_EVENT_ACTIVE, () => {
                this.addEventInfo(CLIENT_EVENT_ACTIVE, 'receive event from client')
            })

            ClientApi.on(CLIENT_APP_EVENT_DESTROY, () => {
                this.addEventInfo(CLIENT_APP_EVENT_DESTROY, 'receive event from client')
                ClientApi.destroy()
                ClientApi.emit({
                    eventName: WEB_APP_EVENT_END,
                    eventData: {}
                })
                this.addEventInfo(WEB_APP_EVENT_END, 'emit event to client')
            })

            ClientApi.emit({
                eventName: WEB_APP_EVENT_READY,
                eventData: {}
            })
            this.addEventInfo(WEB_APP_EVENT_READY, 'emit event to client')
        }
    }
</script>

<style scoped>

    h2 {
        font-weight: normal;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: block;
        margin: 0 10px;
    }

</style>



